@import url('https://fonts.googleapis.com/css2?family=Alata&family=Open+Sans:ital,wght@0,300;0,400;0,600;1,400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,600;1,400&display=swap');
:root {
    --blue: #007bff;
    --indigo: #6610f2;
    --purple: #6f42c1;
    --pink: #dd00a1;
    --red: #dc3545;
    --orange: #F88600;
    --yellow: #f8b600;
    --green: #28a745;
    --teal: #20c997;
    --cyan: #17a2b8;
    --white: #fff;
    --gray: #6c757d;
    --gray-dark: #343a40;
    --primary: #007bff;
    --secondary: #6c757d;
    --success: #28a745;
    --info: #17a2b8;
    --warning: #ffc107;
    --danger: #dc3545;
    --light: #f8f9fa;
    --dark: #343a40;
/* Starburst colors */
    --midnight-blue: #000A2C;
    --starlight-blue: #165FF2;
    --aurora-teal: #14D9B5;
    --lunar-blue: #16DCF2;
    --pulsar-purple: #8D29B8;
    --fusion-fuchsia: #B835A1;
    --starburst-teal: #00A7B5;
    --breakpoint-xs: 0;
    --breakpoint-sm: 576px;
    --breakpoint-md: 768px;
    --breakpoint-lg: 992px;
    --breakpoint-xl: 1200px;
    --font-family-sans-serif: 'Montserrat', sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    --font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  /* Starburst material colours */
    --primary-50: #e1e3e8;
    --primary-100: #b4b9c6;
    --primary-200: #828aa0;
    --primary-300: #4f5b79;
    --primary-400: #2a375d;
    --primary-500: #041440;
    --primary-600: #03123a;
    --primary-700: #030e32;
    --primary-800: #020b2a;
    --primary-900: #000A2C;
    --secondary-50: #e0f4f6;
    --secondary-100: #b3e5e9;
    --secondary-200: #80d3da;
    --secondary-300: #4dc1cb;
    --secondary-400: #26b4c0;
    --secondary-500: #00a7b5;
    --secondary-600: #009fae;
    --secondary-700: #0096a5;
    --secondary-800: #008c9d;
    --secondary-900: #007c8d;
    --top-spacing: 80px;
  }

.bg-opacity-0 {--bg-opacity: 0;}
.bg-opacity-25 {--bg-opacity: 0.25;}
.bg-opacity-50 {--bg-opacity: 0.5;}
.bg-opacity-75 {--bg-opacity: 0.75;}
.bg-opacity-100 {--bg-opacity: 1;}

html { scroll-behavior: smooth;scroll-padding-top: var(--top-spacing); }
html, body { height: 100%;margin:0; }
body {display: flex;flex-direction: column;font-family: 'Open Sans', sans-serif;font-size: .9em;font-weight:400;letter-spacing: 0.04rem; }
.wrap { flex: 1 0 auto; }
.content {min-height: calc(100vh - 380px);}
.toc-content {position: relative;}

/*----- HEADER -----*/
.navbar-dark { background-color: var(--midnight-blue); }
.nav-item {
  font-family: 'Open Sans', sans-serif;
  font-size: 0.8em;
  font-weight:400;
  letter-spacing:0.12em;
  text-transform: uppercase; }

// specific to overtake mdb.css
.navbar.navbar-dark .navbar-nav .nav-item .nav-link.active {
  color: #ED6AD6;
  font-weight: 600;
}

.navbar-left-container {
  display: flex;
  align-items: center;

  .nav-item {
    color: #ffffff;
  }
}

.navbar-divider {
  border-right: 1px solid #ffffff;
  align-self: normal;
}

/*----- FOOTER -----*/
footer {
  flex-shrink: 0;
  margin-top: auto;
}

.page-footer {
  display: flex;
  flex-direction: row;
  height: 280px;
  background: var(--midnight-blue);
  margin-top: 20px;
	align-items: center;
  justify-content: space-evenly;
  font-size: 13px;
  bottom:0;
  width:100%;
}

.page-footer ul { padding-inline-start: 0px; }
li.footer-item { list-style-type: none;line-height: 23px; }
a.footer-icon {color: white;font-size:20px;margin:0px 15px;}
a.footer-icon:hover {color:var(--starburst-teal);}

a.header-link {color:var(--fusion-fuchsia);}
a.header-link:hover {color:var(--pulsar-purple);}

/*----- TYPOGRAPHY -----*/
h1, h2, h3, h4, h5, h6 { font-family: 'Montserrat', sans-serif;font-weight:bold;margin-top:1rem; }
.body-1 {font-size:18px;letter-spacing:0.5px;}
.body-2 {font-size:16px;letter-spacing:0.25px;}
.subtitle-1 {font-size:18px;letter-spacing:0.15px;}
.subtitle-2 {font-size:16px;letter-spacing:0.1px;}
.button {font-size:16px;letter-spacing:1.25px;text-transform:uppercase;}
.btn-primary {background-color: var(--starburst-teal) !important;}
.caption {font-size:14px;letter-spacing:0.4px;}
.overline {font-size:14px;letter-spacing:2px;text-transform:uppercase;}
a { color:var(--starburst-teal); }
a:hover, a:active { color:var(--secondary-900); }
.spacer-30 { margin-top:30px; }
.spacer-60 { margin-top:60px; }
.center { text-align:center; }
.breadcrumbs { font-size:12px; }
.card-body p {font-size:14px;font-weight:light;color:black;}

/*----- IMAGES -----*/
.img-center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: 90%; }
.img-thumbnail {
  padding: .25rem;
  background-color: white;
  border: 0px;
  border-radius: .25rem;
  max-width: 100%;
  height: auto; }
.img-screenshot {
  border: 1px solid var(--midnight-blue);}

.modal {}
.modal-content {margin:auto;padding:10px;}
.button-img {border:none;background:none;}
@media (min-width: 992px) {
  .modal-lg, .modal-xl {max-width: 1000px; }
}

/*----- VIDEOS -----*/
.embed-container {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  height: 0;
  margin-bottom: 1rem; }
.embed-container iframe, .embed-container .wistia_embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

/*----- TABLES -----*/
table {
    display: table;
    border-collapse: collapse;
    border-spacing: 2px;
    border-color: grey;
    max-width: 100%;
    min-width: 50%;
    background-color: transparent;
    margin: 30px 0px;
    border: 1px solid var(--primary-50); }

thead {
    background-color: var(--primary-500);
    color:white;
    display: table-header-group;
    vertical-align: middle;
    border-color: inherit;
    font-weight: bold;
    border: 1px solid var(--primary-500); }

table th {
    background-color: var(--midnight-blue);
    color:white;
    padding-top: 1.1rem;
    padding-bottom: 1rem;
    padding: .75rem;
    border: 1px solid var(--primary-500); }

table td  {
    padding-top: 1.1rem;
    padding-bottom: 1rem;
    padding: .75rem;
    vertical-align: top;
    border-bottom: 1px solid var(--primary-50); }

table tr:nth-child(even){background-color: var(--light);}

/*----- SIDE NAVIGATION STYLES -----*/
.side-navigation { position: sticky;position: -webkit-sticky; top:60px;padding:0px 40px; }
.side-navigation li { line-height: 24px;list-style-type: none; }
.docs-content { padding-right: 30px;padding-left: 30px; }
.hidden { display:none; }
.left-nav { flex: 1; margin-left: 14px; }
.left-nav li { font-size: .85rem;margin-bottom: 6px;list-style-type: none; }
.left-nav li[onclick] {cursor: pointer;}
.left-nav li a, .left-nav li a:not([href]):not([tabindex]) { color: #212529; }
.left-nav li:hover a:not([href]):not([tabindex]), .left-nav li a:hover, .left-nav li a:not([href]):not([tabindex]):hover { color: var(--starburst-teal); }
.left-nav li.active a, .left-nav .doc-subnav li.active a, .left-nav .doc-subfolder li.active a {
  color: var(--starburst-teal); }
.left-nav li.active > a > i {
  transform: rotate(-90deg); //flips menu dropdown icon down
}
.left-nav .doc-subnav li {
  line-height: 20px;
  margin-bottom: 6px;
  margin-left: -20px;
  text-indent: -.5em; }
.left-nav .doc-subnav li a, .left-nav .doc-subfolder li a { font-size: .85rem; }
.left-nav .doc-subfolder li {
  line-height: 14px;
  margin-bottom: 14px;
  margin-left: -40px; }

//same line-height as parent container to vertically center icon
.nav-icon { float:right;line-height: 24px;}


/*----- TOC STYLES -----*/
.toc-styles {
  padding: 0px 40px;
  /*background-color: #F0F0F0;
  box-shadow: inset 7px 0 9px -7px rgba(0,0,0,0.2);*/}
.toc-styles ul {font-size: 0.8rem;padding-inline-start: 1rem;border-left: 0.15rem solid var(--starburst-teal);}
.toc-styles li {list-style-type: none;margin-top: .625rem;line-height:1.6em;}
.toc-styles li a {color:var(--midnight-blue);}
.toc-styles ul ul {border-left:none;}
.toc-sticky {position: sticky;position: -webkit-sticky; top:120px;}

@media only screen and (max-width: 1024px) {
  .side-navigation {padding: 0 30px;}
}
@media only screen and (max-width: 768px) {
  .side-navigation {position:relative;top:0px;padding: 0 20px;}
  .toc-sticky {position:relative;top:0px;}
}
/*----- HOMEPAGE STYLES -----*/
.homepage-hero {
    /*background: url(/assets/img/universe-home.png);*/
    background: url(/assets/img/bg/dust-pink.jpg);
    background-size: cover;
    background-position: center;
    background-color: var(--midnight-blue);
