/*----- DOCUMENTATION CONTENT STYLES -----*/
.docs-content {
  h1, h2, h3, h4, h5, h6 {
    margin-top: 1.2rem;
    margin-bottom: 1.2rem;
  }
  table {
    margin-top: 1.2rem;
    margin-bottom: 1.2rem;
  }
  pre {
    margin: 1.2rem 5px;
  }
  figure {
    margin-top: 1.2rem;
    margin-bottom: 1.2rem;
  }
}

.row-with-logo {
  display: flex;
  justify-content: space-between;
  flex-direction: row;


  &-text-container {
    flex: 3;
  }

  &-image-container {
    display: flex;
