  }
}

@media (max-width: 767px) {
  .logo-card {
    height: 60vw;
    max-height: unset;
  }
}

/*----- FEEDBACK BLOCK STYLES -----*/
.feedback-container {
  border-left: 3px solid var(--starburst-teal);
  padding: 2rem;
  background-color: rgba(0,0,0,0.025);
  position: relative;

  .feedback-thumbs {
    display: flex;
    align-items: center;

    p {
      margin-bottom: 0;
      margin-right: 1rem;
    }

    .feedback-option {
      cursor: pointer;
      font-size: 0.85rem;
      transition: color 100ms ease-in-out;

      &:hover {
        color: var(--starburst-teal);
      }

      i {
        margin-right: 0.25rem;
      }
      &.active {
        color: var(--starburst-teal);
      }
    }
  }

  .feedback-message {
    display: none;
    align-items: center;

    textarea {
      width: 320px;
      text-indent: 0.5rem;
      margin-right: 2rem;
    }
  }

  a.feedback-skip {
    color: var(--starburst-teal);
    text-decoration: underline;
    margin-left: 1rem;
  }
}

/* Internal Markdown Pages */
.icon-grid {
  display: grid;
  grid-template-columns: repeat(5, minmax(0,1fr));
  gap: 1rem 2rem;

  img {
    max-height: 3.5rem;
    max-width: 3.5rem;
    padding: 0.25rem;
  }

  p {
    margin-bottom: 0;
  }
}

